<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="show">
      <c-header title="常旅客导入(因公)" leftIcon1="return.png" :leftFn1="close"></c-header>

      <div class="search-bar">
        <div class="bar">
          <input maxlength='50' v-model.trim="KeyWord" :placeholder="$t('base.PleaseEnterAKeywordToSearch')"/>
          <div class="logo" style="background-image: url('static/image/plane-ticket/book/search.png')" @click="_getDatalist"></div>
        </div>
      </div>

      <hs-scroll
        class="table-view"
        ref="tableView"
        :data="datalist"
        :options="tableViewOptions"
        @pulling-down="_getDatalist"
        @pulling-up="_getDatalistMore">
        <ul>
          <div style="width: 100%; height: 1px;"></div>
          <hs-null v-if="!datalist || datalist.length === 0"></hs-null>
          <div v-for="(item, index) in datalist" :key="index"
            class="custom-cell-passenger">
            <div class="details">
              <div class="header">
                <p class="name">
                  {{item.ChName}}
                  <template v-if="item.LastName && item.FirstName">{{item.LastName}}&nbsp;{{item.FirstName}}</template>
                </p>
                <div class="staff" v-if="item.UpType === 1">员工</div>
                <div class="no-staff" v-if="item.UpType === 2">非员工</div>
              </div>
              <div class="item" v-if="item.Credentials.filter(v => v.IsDefault)[0] && item.Credentials.filter(v => v.IsDefault)[0].CredentialNo">
                <p>
                  {{item.Credentials.filter(v => v.IsDefault)[0].CredentialName}}
                </p>
                <p>
                  {{item.Credentials.filter(v => v.IsDefault)[0].CredentialNo}}
                </p>
              </div>
              <div class="item" v-if="item.Mobile">
                <p>{{$t('base.CellphoneNumber')}}</p>
                <p>{{item.Mobile}}</p>
              </div>
            </div>
            <div class="footer" v-if="!item.Added">
              <p @click="_import(item, index)">{{$t('base.Add')}}</p>
            </div>
          </div>
          <div style="width: 100%; height: 10px;"></div>
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsNull from 'base/hs-null/hs-null'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsTab from 'base/hs-tab/hs-tab'
import {kpiUserGetBookPersons, kpiFrequentTravelerImport} from 'api'
export default {
  data() {
    return {
      show: false,
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      datalist: [],
      currentPageIndex: 1,
      currentPageSize: 30,
      KeyWord: ''
    }
  },
  watch: {
    show(val) {
      if (val) {
        this._getDatalist()
      }
    }
  },
  methods: {
    open() {
      this.show = true
    },
    close() {
      this.show = false
    },
    _getDatalist() {
      this.currentPageIndex = 1
      this.tableViewOptions.pullUpLoad = false
      let params = {
        KeyWord: this.KeyWord,
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        TravelType: 0,
        ImportType: 1 // 常旅客
      }
      kpiUserGetBookPersons(params).then((res) => {
        console.log('常旅客列表： ', res)
        let list = res.ResultData
        this.datalist = list
        if (res.ResultData.length >= this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },
    _getDatalistMore() {
      this.currentPageIndex++
      let params = {
        KeyWord: this.KeyWord,
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        TravelType: 0,
        ImportType: 1 // 常旅客
      }
      kpiUserGetBookPersons(params).then((res) => {
        console.log('常旅客列表： ', res)
        let list = res.ResultData
        this.datalist = this.datalist.concat(list)
        if (res.ResultData.length < this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = false
        } else {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },

    /** 添加 */
    _import(item, index) {
      let parmas = {
        ID: item.ID,
        TravelType: 0
      }
      kpiFrequentTravelerImport(parmas).then((res) => {
        console.log('导入: ', res)
        this.$set(this.datalist[index], 'Added', true)
      })
    }
  },
  components: {
    CHeader,
    HsCell1,
    HsNull,
    HsScroll,
    HsSection,
    HsTab
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  @extend .flex-column
  @extend .flex-start
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_gray_background_0
  z-index 200

  >.search-bar
    @extend .flex-center
    width 100%
    padding 5px 15px
    background-color $color_white
    >.bar
      @extend .flex-row
      @extend .flex-between
      width 100%
      border-radius 4px
      border 1px solid $color_gray_layer
      padding 10px
      background-color $color_white
      font-size 0.9rem
      >input
        width calc(100% - 30px)
        height 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%

  .table-view
    width 100%
    height calc(100% - 45px - 53px)
    overflow hidden

    .custom-cell-passenger
      width calc(100% - 20px)
      padding 15px
      margin-left 10px
      margin-top 10px
      background-color $color_white
      border-radius 3px
      box-shadow 0 0 10px $color_gray_boxshadow
      >.details
        width 100%
        >.header
          @extend .flex-row
          @extend .flex-start
          width 100%
          padding 5px 0
          >.name
            font-size 1.1rem
            font-weight bold
          >.staff
          >.no-staff
            padding 2px 5px
            border-radius 3px
            border 1px solid $color_green_layer
            color $color_green_layer
            font-size 0.7rem
            background-color $color_green_background_0
            margin-left 10px
          >.no-staff
            border 1px solid $color_theme
            color $color_theme
            background-color $color_red_background_0
        >.mark
          font-size 0.8rem
          color $color_theme
          margin-top 5px
        >.item
          @extend .flex-row
          @extend .flex-start
          width 100%
          margin-top 10px
          >p
            font-size 0.9rem
            line-height 1.2em
            color $color_gray_text
            &:nth-child(1)
              width 80px
              margin-right 10px
            &:nth-child(2)
              width calc(100% - 90px)
      >.footer
        @extend .flex-row
        @extend .flex-end
        width 100%
        >p
          @extend .flex-center
          background-color $color_gray_background_0
          height 30px
          border-radius 15px
          padding 0 15px
          font-size 0.9rem
          color $color_black_text_1
</style>



