
<template>
  <transition name="opacity">
    <div class="hs-dialog-menu" v-if="show" @click="close">
      <div class="details" ref="Details" @click.stop>
        <slot></slot>
      </div>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
export default {
  data() {
    return {
      show: false
    }
  },
  methods: {
    open() {
      this.show = true
      setTimeout(() => {
        this.$refs.Details.style.transform = 'scale(1, 1)'
        this.$refs.Details.style.opacity = 1
      }, 100)
    },
    close() {
      this.$refs.Details.style.transform = 'scale(0.1, 0.1)'
      this.$refs.Details.style.opacity = 0
      setTimeout(() => {
        this.show = false
      }, 200)
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-dialog-menu
  position absolute
  top 0
  left 0
  width 100%
  height 100%
  background-color rgba(0, 0, 0, 0.1)

  >.details
    padding 10px 0
    background-color $color_white
    border-radius 5px
    position absolute
    top 60px
    right 5px
    transition all .2s
    box-shadow 0 0 2px $color_gray_boxshadow
    transform scale(0.1, 0.1)
    transform-origin 100% 0 0

    &:after
      position absolute
      content ''
      border-top 10px transparent dashed
      border-left 8px transparent dashed
      border-bottom 10px $color_white solid
      border-right 8px transparent dashed
      right 17px
      top -20px
</style>



