<template>
  <div class="hs-container">
    <c-header isBackNav :title="$t('base.FrequentTravelerManagement')" fixedBackPath="/management">
      <div class="c-header-right" slot="right">
        <div @click="$refs.HsDialogMenu.open()"
          class="logo" style="background-image: url('static/image/header/add.png')"></div>
      </div>
    </c-header>

    <div class="search-bar">
      <div class="bar">
        <input maxlength='50' v-model.trim="KeyWord" :placeholder="$t('base.PleaseEnterAKeywordToSearch')"/>
        <div class="logo" style="background-image: url('static/image/plane-ticket/book/search.png')" @click="_getDatalist"></div>
      </div>
    </div>

    <hs-tab v-if="userManageInfo && userManageInfo.TravelerManageRole === 0"
     :selectedIndex="travelType" :options="tabOptions" @select="_setTravelType"></hs-tab>

    <hs-scroll
      class="table-view"
      :style="{height: userManageInfo && userManageInfo.TravelerManageRole === 0 ? 'calc(100% - 45px - 53px - 40px)' : 'calc(100% - 45px - 53px)'}"
      ref="tableView"
      :data="datalist"
      :options="tableViewOptions"
      @pulling-down="_getDatalist"
      @pulling-up="_getDatalistMore">
      <ul>
        <div style="width: 100%; height: 1px;"></div>
        <hs-null v-if="!datalist || datalist.length === 0"></hs-null>
        <div v-for="(item, index) in datalist" :key="index"
          class="custom-cell-passenger">
          <div class="details">
            <div class="header">
              <p class="name">
                {{item.ChName}}
                <template v-if="item.LastName && item.FirstName">{{item.LastName}}&nbsp;{{item.FirstName}}</template>
              </p>
              <div class="staff" v-if="item.UpType === 1">员工</div>
              <div class="no-staff" v-if="item.UpType === 2">非员工</div>
            </div>
            <!-- <p class="mark">基本信息不全</p> -->
            <div class="item" v-if="item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0] && item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0].CredentialNo">
              <p>
                {{item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0].CredentialName}}
              </p>
              <p>
                {{item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0].CredentialNo}}
              </p>
            </div>
            <div class="item" v-if="item.Mobile">
              <p>{{$t('base.CellphoneNumber')}}</p>
              <p>{{item.Mobile}}</p>
            </div>
          </div>
          <div class="footer">
            <p @click="_gotoDetails(index, item)">{{$t('base.Edit')}}</p>
          </div>
        </div>
        <div style="width: 100%; height: 10px;"></div>
      </ul>
    </hs-scroll>

    <!-- 添加弹窗 -->
    <transition name="opacity">
      <div v-if="showDialog" @click="showDialog = false"
        class="dialog">
        <div class="details" @click.stop>
          <div class="item" @click="_addPassenger(0)">
            <div class="logo" style="background-image: url('static/image/personal-center/passengers/add-business.png')"></div>
            <p>常旅客添加(因公)</p>
          </div>
          <div class="item" @click="_importPassenger()">
            <div class="logo" style="background-image: url('static/image/personal-center/passengers/import-business.png')"></div>
            <p>常旅客导入(因公)</p>
          </div>
          <div class="item" @click="_addPassenger(2)">
            <div class="logo" style="background-image: url('static/image/personal-center/passengers/add-personal.png')"></div>
            <p>常旅客导入(因私)</p>
          </div>
        </div>
      </div>
    </transition>

    <!-- 常旅客详情 -->
    <passenger-details
      ref="PassengerDetails"
      :details="passengerDetails"
      :OrderBusinessType="0"
      @delegate="_delegateWithPassengerDetails"
      @delegateWithDelete="_delegateWithDeletePassenger"
    ></passenger-details>

    <!-- 导入列表 -->
    <person ref="Person"></person>

    <!-- 添加弹窗 -->
    <hs-dialog-menu ref="HsDialogMenu">
      <div class="add-dialog" @click="$refs.HsDialogMenu.close()">
        <div v-if="userManageInfo && userManageInfo.IsAllowAddTraveler" class="item" @click="_addPassenger(0)">
          <div class="logo" style="background-image: url('static/image/personal-center/passengers/add-business.png')"></div>
          <p>添加(因公)</p>
        </div>
        <div v-if="userManageInfo && (userManageInfo.TravelerManageRole === 0 || userManageInfo.TravelerManageRole === 1)" class="item" @click="_importPassenger()">
          <div class="logo" style="background-image: url('static/image/personal-center/passengers/import-business.png')"></div>
          <p>导入(因公)</p>
        </div>
        <div v-if="userManageInfo && (userManageInfo.TravelerManageRole === 0 || userManageInfo.TravelerManageRole === 2)" class="item" @click="_addPassenger(2)">
          <div class="logo" style="background-image: url('static/image/personal-center/passengers/add-personal.png')"></div>
          <p>添加(因私)</p>
        </div>
      </div>
    </hs-dialog-menu>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsNull from 'base/hs-null/hs-null'
import HsDialogMenu from 'base/hs-dialog/hs-dialog-menu'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsTab from 'base/hs-tab/hs-tab'
import PassengerDetails from './details'
import Person from './person'
import {kpiFrequentTravelerList, kpiFrequentTravelerPut, kpiFrequentTravelerAdd, kpiUserGetUserManageInfo} from 'api'

export default {
  created() {
    this._getUserManageInfo()
    this._getDatalist()
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      tabOptions: [],
      details: {},
      datalist: [],
      currentPageIndex: 1,
      currentPageSize: 30,
      KeyWord: '',
      /** 添加弹窗 */
      showDialog: false,
      /** 旅游类型 */
      travelType: 0,
      /** 常旅客详情 */
      passengerDetails: {},
      /** 添加类型 */
      TYPE_ADD_BUSINESS: 0,
      TYPE_IMPOER_BUSINESS: 1,
      TYPE_ADD_PERSONAL: 2,
      /********************/
      /** 常旅客管理权限 */
      userManageInfo: undefined
    }
  },
  watch: {
    travelType() {
      this._getDatalist()
    }
  },
  methods: {
    /** 获取成本中心，部门，BU权限 */
    _getUserManageInfo() {
      kpiUserGetUserManageInfo({}).then((res) => {
        console.log('获取成本中心，部门，BU权限: ', res)
        this.userManageInfo = res.ResultData
        if (this.userManageInfo.TravelerManageRole === 0) {
          this.tabOptions = [this.$t('base.Business'), this.$t('base.Personal')]
          this.travelType = 0
        } else if (this.userManageInfo.TravelerManageRole === 1) {
          this.tabOptions = [this.$t('base.Business')]
          this.travelType = 0
        } else if (this.userManageInfo.TravelerManageRole === 2) {
          this.tabOptions = [this.$t('base.Personal')]
          this.travelType = 1
        }
      })
    },

    /** 获取常旅客列表 */
    _getDatalist() {
      this.details = {}
      this.datalist = []
      this.currentPageIndex = 1
      this.tableViewOptions.pullUpLoad = false
      let params = {
        KeyWord: this.KeyWord,
        OrderBusinessType: 0, // 无
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        TravelType: this.travelType
      }
      kpiFrequentTravelerList(params).then((res) => {
        console.log('获取常旅客列表: ', res)
        this.details = res.ResultData
        this.datalist = res.ResultData.FrequentTravelers
        if (res.ResultData.length >= this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },
    /** 获取更多常旅客列表 */
    _getDatalistMore() {
      this.currentPageIndex++
      let params = {
        KeyWord: this.KeyWord,
        OrderBusinessType: 0, // 无
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        TravelType: this.travelType
      }
      kpiFrequentTravelerList(params).then((res) => {
        console.log('常旅客列表： ', res)
        this.datalist = this.datalist.concat(res.ResultData.FrequentTravelers)
        if (res.ResultData.length < this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = false
        } else {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },

    /** 添加常旅客 */
    _addPassenger(type) {
      this.showDialog = false
      this.passengerDetails = {}
      this.$set(this.passengerDetails, 'ChName', '')
      this.$set(this.passengerDetails, 'FirstName', '')
      this.$set(this.passengerDetails, 'LastName', '')
      this.$set(this.passengerDetails, 'Gender', true)
      this.$set(this.passengerDetails, 'Birthday', this.beTime(new Date()))
      this.$set(this.passengerDetails, 'Email', '')
      this.$set(this.passengerDetails, 'Mobile', '')
      this.$set(this.passengerDetails, 'Department', null)
      this.$set(this.passengerDetails, 'CostCenter', null)
      this.$set(this.passengerDetails, 'BusinessUnit', null)
      this.$set(this.passengerDetails, 'Credentials', this.details.SupportCredentialTypes.map(v => {
        return {
          CredentialName: v.CredentialName,
          CredentialType: v.CredentialType,
          CredentialNo: '',
          EffectiveDate: ''
        }
      }))
      this.$set(this.passengerDetails, 'UpType', type !== this.TYPE_ADD_PERSONAL ? 1 : 2)
      this.$set(this.passengerDetails, 'Nation', null)
      this.$set(this.passengerDetails, 'TravelType', type !== this.TYPE_ADD_PERSONAL ? 0 : 1)
      this.$set(this.passengerDetails, 'TravelCardList', [])
      this.$set(this.passengerDetails, 'DefaultCredentialType', 1)
      this.$set(this.passengerDetails, 'DefaultNameType', 0)
      // this.$set(this.passengerDetails, 'AddType', type)
      if (this.$refs.PassengerDetails) {
        this.$refs.PassengerDetails.open()
      }
    },

    /** 导入常旅客 */
    _importPassenger() {
      this.showDialog = false
      if (this.$refs.Person) {
        this.$refs.Person.open()
      }
    },

    /** 修改常旅客去详情页面 */
    _gotoDetails(index, item) {
      this.passengerDetails = item
      this.$set(this.passengerDetails, 'index', index)
      if (this.$refs.PassengerDetails) {
        this.$refs.PassengerDetails.open()
      }
    },

    /** tab切换 */
    _setTravelType(val) {
      this.travelType = val
    },

    /** 常旅客详情回调 */
    _delegateWithPassengerDetails(item) {
      let params = item
      params.OrderBusinessType = 0
      // 编辑常旅客详情
      if (item.index >= 0) {
        kpiFrequentTravelerPut(params).then((res) => {
          console.log('修改: ', res)
          if (res.ResultData === true) {
            this.$toast(this.$t('TM.SaveSucceed'))
            this.datalist.splice(item.index, 1, item)
          } else {
            this.$toast(this.$t('TM.SaveFailed'))
          }
        })
      } else {
        kpiFrequentTravelerAdd(params).then((res) => {
          console.log('常旅客添加(因公): ', res)
          this.$toast(this.$t('TM.AddSucceed'))
          this._getDatalist()
        })
      }
    },

    /** 移除常旅客回调 */
    _delegateWithDeletePassenger(index) {
      this.datalist.splice(index, 1)
    }
  },
  components: {
    CHeader,
    HsCell1,
    HsNull,
    HsDialogMenu,
    HsScroll,
    HsSection,
    HsTab,
    PassengerDetails,
    Person
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  .c-header-right
    @extend .flex-row
    @extend .flex-end
    width 100%
    height 100%
    .logo
      width 30px
      height 30px
      background-size 100% 100%

  >.search-bar
    @extend .flex-center
    width 100%
    padding 5px 15px
    background-color $color_white
    >.bar
      @extend .flex-row
      @extend .flex-between
      width 100%
      border-radius 4px
      border 1px solid $color_gray_layer
      padding 10px
      background-color $color_white
      font-size 0.9rem
      >input
        width calc(100% - 30px)
        height 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%

  .table-view
    width 100%
    overflow hidden

    .custom-cell-passenger
      width calc(100% - 20px)
      padding 15px
      margin-left 10px
      margin-top 10px
      background-color $color_white
      border-radius 3px
      box-shadow 0 0 10px $color_gray_boxshadow
      >.details
        width 100%
        >.header
          @extend .flex-row
          @extend .flex-start
          width 100%
          padding 5px 0
          >.name
            font-size 1.1rem
            font-weight bold
          >.staff
          >.no-staff
            padding 2px 5px
            border-radius 3px
            border 1px solid $color_green_layer
            color $color_green_layer
            font-size 0.7rem
            background-color $color_green_background_0
            margin-left 10px
          >.no-staff
            border 1px solid $color_theme
            color $color_theme
            background-color $color_red_background_0
        >.mark
          font-size 0.8rem
          color $color_theme
          margin-top 5px
        >.item
          @extend .flex-row
          @extend .flex-start
          width 100%
          margin-top 10px
          >p
            font-size 0.9rem
            line-height 1.2em
            color $color_gray_text
            &:nth-child(1)
              width 80px
              margin-right 10px
            &:nth-child(2)
              width calc(100% - 90px)
      >.footer
        @extend .flex-row
        @extend .flex-end
        width 100%
        >p
          @extend .flex-center
          background-color $color_gray_background_0
          height 30px
          border-radius 15px
          padding 0 15px
          font-size 0.9rem
          color $color_black_text_1

  .add-dialog
    width 100%
    >.item
      @extend .flex-row
      @extend .flex-start
      width 100%
      height 50px
      padding-left 20px
      padding-right 30px
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-right 10px
      >p
        color $color_black_text_1
</style>



